searchModalInit = function () {
  var openSearchModal = function () {
    $(".bs-search-modal").addClass("--display");
    $("body").addClass("--no-scroll");
    $(".bs-search-modal__search-text").focus();
  };


  var closeSearchModal = function () {
    $(".bs-search-modal").removeClass("--display");
    $("body").removeClass("--no-scroll");
  };

  $(".bs-search-modal").click(function() {
    $(".bs-search-modal__search-text").focus();
  });

  $(".bs-search-modal__open-modal").click(openSearchModal);

  $(".bs-search-modal__close-modal").click(closeSearchModal);

  $(".bs-search-modal__search-text").keydown(function(event) {
    var code = event.keyCode || event.which;
    if(code === 13) {
      closeSearchModal();
    }
  });
};
