lyHeroInit = function () {
  $(function() {
    $('.bs-ly-hero__head--dynamic').data('size','big');
  });

  $(window).scroll(function() {
    if($(document).scrollTop() > 0)
    {
      if($('.bs-ly-hero__head--dynamic').data('size') === 'big')
      {
        $('.bs-ly-hero__head--dynamic').data('size','small');
        $('.bs-ly-hero__head--dynamic').addClass("bs-ly-hero--small");
        $('.bs-ly-hero__head--dynamic').stop().animate({
          height:'72px'
        },300);
      }
    }
    else
    {
      if($('.bs-ly-hero__head--dynamic').data('size') === 'small')
      {
        $('.bs-ly-hero__head--dynamic').data('size','big');
        $('.bs-ly-hero__head--dynamic').removeClass("bs-ly-hero--small");
        $('.bs-ly-hero__head--dynamic').stop().animate({
          height:'300px'
        },300);
      }
    }
  });
};
